.burger-input {
	display: none; }
.burger-toggle {
	display: none;
	position: absolute;
	width: 30px;
	height: 30px;
	top: 40px;
	right: 50px;
	cursor: pointer;
	z-index: 11; }
.burger-label {
	display: none;
	position: absolute;
	width: 30px;
	height: 5px;
	top: 40px;
	right: 50px;
	border-radius: 3px;
	background-color: #fff;
	cursor: pointer;
	z-index: 10;
	&::after {
		content: "";
		position: absolute;
		display: block;
		width: 30px;
		height: 5px;
		border-radius: 3px;
		background-color: inherit;
		top: 20px; }
	&::before {
		content: "";
		position: absolute;
		display: block;
		width: 30px;
		height: 5px;
		border-radius: 3px;
		background-color: inherit;
		top: 10px; } }
.slider__slide {
	position: relative;
	height: 1024px; }
#slide1:checked {
	~ #slider-slides .slider-inner {
		margin-left: 0; } }
#slide2:checked {
	~ #slider-slides .slider-inner {
		margin-left: -100%; } }
#slide3:checked {
	~ #slider-slides .slider-inner {
		margin-left: -200%; } }
#slide4:checked {
	~ #slider-slides .slider-inner {
		margin-left: -300%; } }
#slide5:checked {
	~ #slider-slides .slider-inner {
		margin-left: -400%; } }
#slide1:checked ~ #slider-controls label:nth-child(2),
#slide2:checked ~ #slider-controls label:nth-child(3),
#slide3:checked ~ #slider-controls label:nth-child(4),
#slide4:checked ~ #slider-controls label:nth-child(5),
#slide5:checked ~ #slider-controls label:nth-child(1) {
	background: url('../img/chevron-thin-right.svg') no-repeat;
	float: right;
	margin: 0 100px 0 0;
	display: block; }
#slide1:checked ~ #slider-controls label:nth-child(5),
#slide2:checked ~ #slider-controls label:nth-child(1),
#slide3:checked ~ #slider-controls label:nth-child(2),
#slide4:checked ~ #slider-controls label:nth-child(3),
#slide5:checked ~ #slider-controls label:nth-child(4) {
	background: url('../img/chevron-thin-left.svg') no-repeat;
	float: left;
	margin: 0 0 0 100px;
	display: block; }
.slider-overflow {
	width: 100%;
	overflow: hidden; }
#slide1:checked ~ #slider-active label:nth-child(1),
#slide2:checked ~ #slider-active label:nth-child(2),
#slide3:checked ~ #slider-active label:nth-child(3),
#slide4:checked ~ #slider-active label:nth-child(4),
#slide5:checked ~ #slider-active label:nth-child(5) {
	width: 15px;
	height: 15px;
	border: 2px solid #d0beab;
	background-color: transparent; }
.slider-inner {
	display: flex;
	width: 500%;
	overflow-y: hidden;
	transition: all .5s ease; }
.slider__gradient {
		display: block;
		content: "";
		width: 100%;
		height: 100%;
		position: absolute;
		background-image: linear-gradient(to top, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
		top: 0; }
.slider__input {
	display: none; }
.slider-controls {
	position: absolute;
	width: 100%;
	height: 50px;
	top: 45%;
