		width: 50px;
		height: 50px;
		cursor: pointer;
		opacity: 0.6;
		&:hover {
			opacity: 1; } } }
.slider--active {
	position: absolute;
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: center;
	align-self: center;
	width: 100px;
	bottom: 42px;
	left: 909px;
	& label {
		width: 11px;
		height: 11px;
		background-color: #8e8e8e;
		border-radius: 50%;
		cursor: pointer; } }
